export default class extends React.Component {

    static getInitialProps = async ({ query: { id } }) => {
        const response = await fetch(`https://api.audioboom.com/audio_clips/${id}.mp3`);
        const { body: { audio_clip } } = await response.json();
        const description = audio_clip.description;
        const audio = audio_clip.urls.high_mp3;
        const podcastImage = audio_clip.urls.image
        return { description, audio, podcastImage };
    }

    render() {
        const { description, audio, podcastImage } = this.props;
        return (
            <div className="podcast-wrapper">
                <div>
                    <img src={podcastImage} className="podcastPlayerImage" alt="podcast image player"/>
                </div>

                <div className="player-wrapper">
                    <audio controls src={audio} type="audio/mpeg" />
                </div>
                <style jsx>
                    {`
                        .podcast-wrapper {
                            width: 100%;
                            height: 100vh;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            align-items: center;
                        }

                        .podcastPlayerImage {
                            width: 200px;
                            height: 200px;
                            border-radius: 4px;
                        }

                        .player-wrapper {
                            width: 100%;
                            height: 150px;
                            display: flex;
                            justify-content: center;
                            align-items: center;                            
                            background: gray;
                        }

                    `}
                </style>

                <style jsx global>
                    {`
                        body {
                            margin: 0;
                            padding: 0;
                            width: 100%;
                            height: 100%;
                            font-family: system-ui;
                        }
                    
                    `}
                </style>
            </div>
        )
    }
}

